*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html,
body,
#root {
  height: 100%;
}

html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #dbe2ef;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media only screen and (min-width: 768px) {
  .mobile-only {
    display: none !important;
  }
}

@media only screen and (max-width: 767px) {
  .desktop-only {
    display: none !important;
  }
}

.container {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 5rem;
}

.footer {
  height: 2rem;
}

.bold {
  font-weight: bold;
}

.button {
  display: inline-block;
  /* border: 2px solid #113f67; */
  background-color: #113f67;
  color: #f9f7f7;
  font-size: 18px;
  font-weight: 600;
  border-radius: 8px;
  padding: 1rem 2rem;
  margin: 5px 0;
  text-align: center;
  text-decoration: none;
}

.button:hover {
  text-decoration: none;
  cursor: pointer;
}

.page-heading {
  position: relative;
  display: inline-block;
  font-size: 3.2rem;
  color: #113f67;
  padding: 1rem 0 0 2rem;
  margin: 1rem 0 3rem 0;
}

.hash-symbol {
  position: absolute;
  font-size: 6rem;
  color: #f9f7f7;
  font-weight: bold;
  opacity: 0.8;
  padding-right: 1rem;
  top: -9px;
  left: -4px;
  z-index: -1;
}

.dot {
  display: block;
  font-size: 4rem;
  opacity: 0.5;
  width: 100%;
  text-align: center;
}

.navbar-container {
  height: 10%;
  width: 100%;
  display: -ms-flex;
  display: flex;
  padding: 1.6rem 0;
  justify-content: space-between;
}

a {
  text-decoration: none;
  font-size: 2rem;
  font-weight: 400;
  color: #112d4e;
}

.navbar-end-section a {
  padding-left: 2rem;
}

a:hover {
  text-decoration: underline;
}

.selected {
  font-weight: bold;
  outline: none;
}

.navbar-hamburger {
  display: block;
  width: 100%;
  height: 30px;
}

.navbar-hamburger a span {
  padding-right: 5px;
}

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 24px;
  height: 16px;
  right: 20px;
  top: 20px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #113f67;
}

.bm-burger-bars-hover {
  background: #113f67;
  opacity: 1 !important;
}
/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #113f67;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%;
}

/* General sidebar styles */
.bm-menu {
  width: 100%;
  background: #f9f7f7;
  padding: 5rem 1.5rem 0 1.5rem;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #f9f7f7;
}

.bm-item-list {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

/* Individual item */
.bm-item {
  display: inline-block;
  padding: 2rem 0;
  outline: none;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

.link {
  text-decoration: underline;
  padding: 0;
  font-weight: bold;
}

.exp--companyLink {
  font-size: 2.4rem;
}

.blog__heading--author {
  display: block;
  text-decoration: none;
  font-size: 2.4rem;
}

.blog__heading--author:hover {
  text-decoration: underline;
}

.blog__content--link {
  text-decoration: none;
}

.blog__content--link:hover {
  text-decoration: underline;
}

@media only screen and (max-width: 468px) {
  .blog__heading--author {
    font-size: 2rem;
  }
  .blog__content--link {
    font-size: 1.8rem;
  }
}

.homepage-container {
  display: -ms-flex;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1;
}

.homepage-intro {
  height: auto;
  max-width: 500px;
  padding-top: 30px;
  text-align: center;
  flex-grow: 1;
}

.homepage-intro .homepage-intro--text {
  font-size: 21px;
  line-height: 28px;
  color: #113f67;
  text-align: center;
}

.homepage-intro .homepage-intro--heading {
  font-size: 3.2rem;
  color: #113f67;
  margin-bottom: 3rem;
}

.avatar {
  display: -ms-flex;
  display: flex;
  justify-content: center;
  width: 100%;
  min-height: 250px;
}

.avatar img {
  align-self: flex-end;
  height: 300px;
}

.social-icons {
  display: -ms-flex;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  box-sizing: border-box;
  min-height: 160px;
  max-height: 180px;
}

.icon-container {
  margin: 20px 20px 0 20px;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.icon-container:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.github,
.linkedin {
  align-self: flex-end;
}

.instagram,
.twitter {
  align-self: flex-start;
}

.github-icon,
.twitter-icon,
.instagram-icon,
.linkedin-icon {
  color: #009f9d;
  opacity: 1;
}

.github-icon:hover,
.twitter-icon:hover,
.instagram-icon:hover,
.linkedin-icon:hover {
  color: #0f0a3c;
}

.github-icon:hover {
  fill: #34699a;
}

.twitter-icon:hover {
  fill: #8ed6ff;
}

.instagram-icon:hover {
  fill: #e14594;
}
.linkedin-icon:hover {
  fill: #408ab4;
}

@media only screen and (max-width: 468px) {
  .homepage-intro p {
    font-size: 18px;
  }

  .homepage-intro,
  .social-icons {
    width: 90vw;
  }

  .github-icon,
  .twitter-icon,
  .instagram-icon,
  .linkedin-icon {
    height: 40px;
    width: 40px;
  }

  .icon-container {
    margin: 10px 10px 0 10px;
  }

  .instagram,
  .twitter {
    align-self: center;
  }

  .avatar img {
    height: 250px;
  }
}

.aboutme-page-container {
  display: -ms-flex;
  display: flex;
  flex-direction: column;
}

.aboutme {
  font-size: 2rem;
  line-height: 3rem;
  color: #113f67;
  margin-bottom: 2rem;
}

.imp {
  border: 2px solid #113f67;
  padding: 2rem;
  border-radius: 5px;
}

.design-page-container {
  display: -ms-flex;
  display: flex;
  flex-direction: column;
}

.designs {
  margin-top: 20px;
  font-size: 20px;
  line-height: 28px;
  color: #113f67;
}

.project-container {
  display: flex;
  flex-direction: column;
  background-color: #f9f7f7;
  min-height: 28rem;
  border-radius: 1rem;
  box-shadow: 0 1rem 4rem rgba(0, 0, 0, 0.2);
}

.project-container:hover {
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
}

.project__header {
  padding: 1.5rem;
  height: 25%;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.project__header--links {
  display: flex;
  margin-left: 1rem;
}

.project__header--links a {
  padding-left: 1rem;
  outline: none;
}

.project__header--heading {
  color: #113f67;
  font-size: 2.4rem;
  font-weight: bold;
}

.project__content {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  justify-content: space-between;
  height: 80%;
  background-color: #e6e6e6;
  padding: 1.5rem;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
}

.project__content--desc {
  font-size: 1.8rem;
  color: #113f67;
  padding-bottom: 1rem;
}

.project__content--tech {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
}

.project__content--tech .tech-badge {
  color: #113f67;
  background-color: #f9f7f7;
  padding: 5px 10px;
  border-radius: 2rem;
  display: inline-block;
  margin: 0 7px 7px 0;
  font-size: 1.4rem;
}

.experience {
  width: 100%;
  margin-bottom: 5rem;
  border-radius: 1rem;
  box-shadow: 0 1rem 4rem rgba(0, 0, 0, 0.2);
}

.experience:hover {
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
}

.exp-info {
  display: flex;
}

.exp--companyLogo {
  padding: 1rem;
  margin-bottom: 2rem;
  height: 7rem;
  width: 23.5rem;
}

.role-and-duration {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 3px;
}

.exp-info--main {
  width: 375px;
  padding: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #f9f7f7;
  border-top-left-radius: 1rem;
  border-bottom-left-radius: 1rem;
}

.exp-info--sub {
  background-color: #e6e6e6;
  border-top-right-radius: 1rem;
  border-bottom-right-radius: 1rem;
  padding: 3rem;
}

.experience .exp--icon {
  margin-right: 1rem;
}

.exp--role {
  display: block;
  font-size: 2rem;
  color: #113f67;
  padding-bottom: 3px;
  font-weight: bold;
}

.exp--duration {
  display: block;
  color: #113f67;
  font-size: 1.6rem;
}

.exp-info--sub .exp-desc {
  font-size: 1.8rem;
  color: #113f67;
  padding-left: 4px;
  line-height: 2.4rem;
}

@media only screen and (max-width: 768px) {
  .exp-info {
    flex-direction: column;
  }
  .exp-info--main {
    width: 100%;
    border-radius: 0;
    border-top-left-radius: 1rem;
    border-top-right-radius: 1rem;
  }
  .exp-info--sub {
    border-radius: 0;
    border-bottom-left-radius: 1rem;
    border-bottom-right-radius: 1rem;
  }
  .exp-info--sub .exp-desc {
    text-align: justify;
    text-justify: inter-word;
  }
}

.section,
.work__projects {
  margin-bottom: 5rem;
}

.work-section {
  width: 100%;
}

.work__projects--content {
  display: grid;
  grid-gap: 2rem;
  grid-template-columns: repeat(auto-fit, minmax(330px, 1fr));
}

.blog-list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 4rem 0;
}

.blog__content--desc {
  font-size: 2rem;
  color: #3f72af;
  padding: 5px 0 10px 0;
}

.blog hr {
  margin: 5px 0 10px 0;
  opacity: 0.2;
  color: lightgrey;
}

.blog {
  margin-bottom: 1.5rem;
  width: 100%;
}

.blog__content--linkIcon {
  color: #113f67;
  padding-right: 5px;
}

.blog__content--linkContainer {
  display: flex;
  align-items: center;
}

#blogs-i-follow p {
  font-size: 2rem;
  color: #3f72af;
}

@media only screen and (max-width: 468px) {
  .blog__content--desc {
    font-size: 1.8rem;
  }
  #blogs-i-follow p {
    font-size: 1.8rem;
  }
}

.App {
  display: -ms-flex;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 960px;
  min-height: 100%;
  margin: 0 auto;
  padding: 0 2rem;
  font-family: "Work Sans", "Roboto", sans-serif;
}

